import React from 'react';
import './App.css';
import {BrowserRouter as HashRouter, Link, Route} from 'react-router-dom';
import Trainer from "./components/Trainer";
import Terms from "./pages/Terms";
import Imprint from "./pages/Imprint";
import logo from "./images/logo.png"

export default function App() {
    return (
        <HashRouter basename='/'>
            <React.Fragment>
                <header className="Header">
                    <h2><img src={logo}/><Link to="/">Neo Touch Typing Tutor</Link></h2>
                </header>
                <div className="Main">
                    <Route exact path="/" component={Trainer}/>
                    <Route path="/terms" component={Terms}/>
                    <Route path="/imprint" component={Imprint}/>
                </div>
                <footer className="Footer">
                    © Copyright 2020 Nils Schmidt&nbsp;|&nbsp;<Link to="/imprint">Impressum</Link>&nbsp;|&nbsp;<Link
                    to="/terms">Datenschutz</Link>
                </footer>
            </React.Fragment>
        </HashRouter>
    );
}
